import React from 'react'
import Helmet from 'react-helmet'
import GetStarted from '../partials/GetStarted'
import FooterInfo from '../partials/FooterInfo'
import { Accordion, AccordionItem } from 'react-sanfona'

const PartnersPage = () => (
  <div>
    <Helmet
      title="Partners - Fitato"
      meta={[
        { name: 'description', content: 'Fitato partner network.' },
      ]}
    />
    <div className="partners-page">
      <div className="container">
        <h1>Fitato Partner Network</h1>
        <h2>250+ Gyms | 1000+ Activities | One Pass</h2>
        <Accordion allowMultiple>
          <AccordionItem title="Viman Nagar">
            <img src="static/partners/viman-nagar.jpg" />
          </AccordionItem>
          <AccordionItem title="Kalyani Nagar">
            <img src="static/partners/kalyani-nagar.jpg" />
          </AccordionItem>
          <AccordionItem title="Koregaon Park">
            <img src="static/partners/koregaon-park.jpg" />
          </AccordionItem>
          <AccordionItem title="Shivaji Nagar, Deccan & City Areas">
            <img src="static/partners/shivaji-nagar.jpg" />
          </AccordionItem>
          <AccordionItem title="Aundh">
            <img src="static/partners/aundh.jpg" />
          </AccordionItem>
          <AccordionItem title="Baner">
            <img src="static/partners/baner.jpg" />
          </AccordionItem>
          <AccordionItem title="Kothrud">
            <img src="static/partners/kothrud.jpg" />
          </AccordionItem>
          <AccordionItem title="Kondhwa">
            <img src="static/partners/kondhwa.jpg" />
          </AccordionItem>
          <AccordionItem title="Wanowrie &amp; Camp">
            <img src="static/partners/wanowrie.jpg" />
          </AccordionItem>
          <AccordionItem title="PCMC">
            <img src="static/partners/pcmc.jpg" />
          </AccordionItem>
          <AccordionItem title="Pimple Saudagar &amp; Wakad">
            <img src="static/partners/pimple-saudagar-wakad.jpg" />
          </AccordionItem>
        </Accordion>
      </div>
    </div>
    <GetStarted />
    <FooterInfo />
  </div>
)

export default PartnersPage



